
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

import progress from '../common/progress.vue'
export default {
components: {
ViewBox,
XHeader,
Group,
Cell,
'rate': progress
},
data () {
return {
index: 0,
isLoanFinsh: false,
hasApply: true,
canApply: null,
page: ''
}
},
mounted () {
this.initPage()
},
computed: {
step () {

let status = this.$store.state.applyState
if (status === '') {
return ''
} else {
return parseInt(status)
}
},
repaymentObj () {
return this.$store.state.loanInfo.repaymentObj
},
creditObj () {
return this.$store.state.loanInfo.creditObj
},
spendObj () {
return this.$store.state.loanInfo.spendObj
}
},
methods: {
initPage () {
if (this.isLogin() && this.isVerfied()) {
this.initView()
} else {
this.canApply = true
}
},
goApply () {
if (this.isLogin()) {
if (this.isVerfied()) {
this.apply()
} else {

this.toVerfiedPage()
}
} else {
this.$router.push('/login')
}
},
apply () {

let self = this
let path = self.$store.state.baseUrl + '/app/xsyd/applyPageInit.do'
let data = {
action: 'init_request',
path: path,
params: {
token: this.$store.state.userInfo.token
}
}
self.$store.dispatch('normalRequest', data).then(data => {

if (data.response === 'success') {
if (data.data.canApply === 1) { // 1 2
this.$router.push('/loan')
this.$store.state.tabItem = 1
} else {
this.$vux.toast.text(data.data)
}
} else {
this.$vux.toast.text(data.data)
}
})
},
getProgress (type) {
let self = this
switch (type) {
case '1': self.$store.state.applyState = 1; self.page = '/quotaEvaluation' // 
break
case '2': self.$store.state.applyState = 2; self.page = '/fail' // 
break
case '3': self.$store.state.applyState = 3; self.page = '/sign' // 
break
case '4': self.$store.state.applyState = 4; self.page = '/sign' // 
break
case '5': self.$store.state.applyState = 5; self.page = '/success' // 
break
case '6': self.$store.state.applyState = 6; self.page = '/fail' // 
break
case '7': self.$store.state.applyState = 7; self.page = '/sign' // 
break
}
},
initView () {

let self = this
let path = self.$store.state.baseUrl + '/app/xsyd/myLoanInit.do'
let data = {
action: 'init_request',
path: path,
params: {
token: self.$store.state.userInfo.token
}
}
self.$store.dispatch('normalRequest', data).then(data => {
if (data.response === 'success') {
if (data.data.type === '') {
self.canApply = true
} else {
self.canApply = false
self.getProgress(data.data.type)
}
self.$store.state.loanInfo.repaymentObj = data.data.repaymentObj
self.$store.state.loanInfo.creditObj = data.data.creditObj
self.$store.state.loanInfo.spendObj = data.data.spendObj
self.$store.state.applyNo = data.data.applyNum
} else {
this.$vux.toast.text(data.data)
}
})
}
}
}



